<template>
<b-row>
  <b-col sm="12" md="8" lg="8">
    <div class="square">
      <b-img-lazy src="https://lorempixel.com/1024/768/cats/1/" fluid-grow class="my-5"></b-img-lazy>
      <b-button @click="clickToFullScreen"></b-button>
      <b-img src="https://lorempixel.com/1024/400/" class="fullScreen" v-if="fullScreen === true"></b-img>
    </div>
  </b-col>
  <b-col sm="0" md="4" lg="4">
    <div class="square">
      <h1></h1>
    </div>
  </b-col>
</b-row>
</template>

<script>
	export default {
		name: "showcase",
    data () {
		  return {
		    fullScreen: false
      }
    },
    methods: {
		  clickToFullScreen () {
		    let vm = this
        vm.fullScreen = true
      }
    }
	}
</script>

<style scoped>
  .fullScreen {
    height: 100%;
    width: 100%;
    position: fixed;
    left: 50%;
    top: 50%;
    margin: auto
  }
</style>



