<template>
  <b-container id='app' style="position: relative;">
    <b-row>
      <b-col sm="12" md="8" lg="8">
        <div class="square">
          <ul style="padding: 0; margin: 0">
            <li v-for="(obj, index) in testImg.slice(pageStart, pageStart + countOfPage)" :id="'list' + index">
              <h1># {{index + 1}} 測試</h1>
              <div @click="changeToFullScreen(obj)">
                <b-img v-bind:src="obj" class="titleImg" fluid></b-img>
              </div>
              <div style="position: fixed; background-color:rgba(0,0,0,0.16); max-height: 100%; max-width: 100%; height: 100%; width: 100%; top: 50%; left: 50%; z-index: 9900;
              transform: translate(-50%, -50%)" v-if="fScreen === true" class="d-none d-md-block">
                <h1 class="text-white" style="position: absolute; top: 5%; right: 5%;" @click="closeFullScreen">X</h1>
                <div @click="changeToLeft"
                     style="height: 1.5rem; width: 1.5rem; border: .2rem ghostwhite solid; border-top: transparent; border-right: transparent; transform: rotate(45deg); position: absolute; top: 50%; left: 3%; z-index: 9991;"></div>
                <div @click="changeToRight"
                     style="height: 1.5rem; width: 1.5rem; border: .2rem ghostwhite solid; border-left: transparent; border-bottom: transparent; transform: rotate(45deg); position: absolute; top: 50%; right: 3%; z-index: 9991;"></div>
                <div style="position: absolute; width: 100% ;height: 80%; top: 50%; transform: translateY(-50%)">
                  <b-img v-bind:src="fScreenImg" v-if="fScreenImg !== undefined" fluid
                         style="width: 80%; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); max-height: inherit; max-width: inherit;"></b-img>
                  <h1 v-if="fScreenImg === undefined" class="text-white"
                      style="position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%);">沒有圖片可以看ㄛ</h1>
                </div>
              </div>
              <p>{{now}}</p>
            </li>
          </ul>
          <div style="position: relative;">
            <b-button-toolbar aria-label="Toolbar with button groups">
              <b-button-group class="mx-1">
                <b-btn @click="setPage(currentPage - 1)" v-if="totalPage >= 1" >&lsaquo;</b-btn>
              </b-button-group>
              <b-button-group class="mx-1">
                <b-btn v-for="(obj, i) in totalPage" :key="i" @click="setPage(obj)">{{obj}}</b-btn>
              </b-button-group>
              <b-button-group class="mx-1">
                <b-btn @click="setPage(currentPage + 1)" v-if="totalPage >= 1">&rsaquo;</b-btn>
              </b-button-group>
            </b-button-toolbar>
          </div>
        </div>
      </b-col>
      <b-col sm="0" md="4" lg="4" class="d-none d-md-block">
        <div class="square" style="position: fixed;">
          <div style="position: relative; max-width: 20rem;">
            <h1>{{msg}}</h1>
            <a href="#1">lorem</a><br>
            <a href="#2">lorem</a><br>
            <a href="#3">lorem</a><br>
            <a href="#4">lorem</a><br>
          </div>
        </div>
      </b-col>
      <b-col>
        <div class="row">
          <hr class="col-11 text-center" style="background-color: #2E6E48;">
        </div>
        <b-container>
          <div class="row">
          </div>
        </b-container>
      </b-col>
    </b-row>
    <b-btn class="btn-circle" @click="chatOpen" id="chat">123</b-btn>
    <b-popover :target="'chat'"
               :placement="chatPosition"
               title="請聯絡我！"
               :content="h1Text"
               :show.sync="chat">
      <b-form>
        <b-form-group id="emailGroupEmail"
                      label="您的信箱："
                      label-for="emailInput"
                      description="我們不會洩漏您的資訊。">
          <b-form-input id="emailInput"
                        type="email"
                        v-model="inputEmail"
                        required
                        placeholder="請輸入您的信箱。"
          ></b-form-input>
        </b-form-group>
        <b-form-group id="emailGroupName"
                      label="您的大名："
                      label-for="nameInput">
          <b-form-input id="nameInput"
                        type="text"
                        v-model="inputName"
                        required
                        placeholder="請輸入您的大名。"
          ></b-form-input>
        </b-form-group>
        <b-form-textarea id="emailGroupTextArea"
                         v-model="text"
                         placeholder="請輸入您的寶貴意見。"
                         :rows="3"
                         :max-rows="6">
        </b-form-textarea>
        <b-form-group>
          <b-button class="btn-outline-info mt-3">送出</b-button>
        </b-form-group>
      </b-form>
    </b-popover>
  </b-container>
</template>

<script>
  Date.prototype.myDate = function () {
    if (this.getMinutes().length < 2) {
      let minute = JSON.stringify(this.getMinutes()).split("")
      this.myProp = minute.unshift("0")
    } else {
      this.myProp = this.getMinutes()
    }
  }
  let date = new Date()
  date.myDate()
  let minute = date.myProp
  export default {
    name: 'HelloWorld',
    data() {
      return {
        now: `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}  ${date.getHours()}: ${minute}`,
        msg: 'Welcome to Your Vue.js App',
        text: `Quis magna Lorem anim amet.`,
        h1Text: `Quis magna.`,
        testImg: ['https://i.imgur.com/efFTTIT.jpg', 'https://i.imgur.com/AM3lVYo.gif', 'https://i.imgur.com/BwXxYmu.jpg', 'https://i.imgur.com/tZL0CbA.gif'
          , 'https://i.imgur.com/309wjS9.jpg', 'https://i.imgur.com/GynjwN2.jpg', 'https://i.imgur.com/oo4TKze.jpg'],
        fScreen: false,
        fScreenImg: '',
        innerHeight: window.innerHeight,
        innerWidth: window.innerWidth,
        date: date.myProp,
        inputEmail: '',
        inputName: '',
        chatPosition: 'top',
        chat: false,
        currentPage: 1,
        countOfPage: 5
      }
    },
    computed: {
      fullScreenLeft() {
        if (this.fScreenImg !== '') {
          return this.testImg[this.testImg.indexOf(this.fScreenImg) - 1]
        }
      },
      fullScreenRight() {
        if (this.fScreenImg !== '') {
          return this.testImg[this.testImg.indexOf(this.fScreenImg) + 1]
        }
      },
      pageStart() {
        return ((this.currentPage - 1) * this.countOfPage)
      },
      totalPage() {
        return Math.ceil(this.testImg.length / this.countOfPage)
      },
    },
    methods: {
      changeToFullScreen(img) {
        this.fScreenImg = img
        this.fScreen = !this.fScreen
      },
      closeFullScreen() {
        this.fScreenImg = ''
        this.fScreen = !this.fScreen
      },
      changeToLeft() {
        this.fScreenImg = this.fullScreenLeft
      },
      changeToRight() {
        this.fScreenImg = this.fullScreenRight
      },
      chatOpen() {
        this.chat = !this.chat
      },
      setPage(idx) {
        if (idx <= 0  || idx > this.totalPage) {
          return
        }
        this.currentPage = idx
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }
</style>



